.App {
  text-align: center;
  width: 100vw;
  top: 0px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.lowerleft {
  margin-bottom: 1px;
  margin-left: 1px;
  width: 300px;
  height: 300px;
  position: fixed;
  bottom: 0;
}

.lowerRight {
  margin-bottom: 1px;
  margin-right: 1px;
  width: 300px;
  height: 300px;
  position: fixed;
  bottom: 0;
  right: 0;
}

#refreshPage {
  background-color: transparent;
  color: black;
}
#switchLabel {
  margin: 0 0 1rem 0 !important;
}
.Mui-checked {
  color: #44c8f5 !important;
}

.topright {
  margin-right: 1rem;
  margin-top: 1rem;
  top: 0px;
  right: 0px;
}

.multipleOptions {
  display: flex;
  margin-top: 4rem;
}

