  padding: 0;
  background-color: transparent;
  color: black;
  margin-right: 5rem;
  margin-top: 1rem;
  position: absolute;
  top: 0px;
  right: 0px;
}  */

.topleft {
  /* position: relative; */
  margin-left: 1rem;
}

#HamburgerMenuButton {
  padding: 0px;
  margin-right: 2rem;
}

#stepper {
  padding-top: 1rem;
  margin: auto 0;
}

#GoToNextLocation, .btn-next {
  border-radius: 50%;
  text-align: center;
  height: 15rem;
  width: 15rem;
  font-size: x-large;
  font-weight: 600;
  display: block;
  margin: 0 auto;
  background-color: #44c8f5;
  color: white;
}

#qr {
  height: 15rem;
  width: 15rem;
  display: block;
  margin: 0 auto;
}

.ttsText {
  word-wrap: break-word;
}

#ttsDiv {
  width: 70%;
  margin: 0 auto;
  margin-top: 50px;
  font-size: 2rem;
  height: 250px;
  font-weight: 600;
  text-align: center;
}

#cancelButton {
  padding: 0;
  margin: 0rem;
  background-color: transparent;
  margin-right: auto;
  display: block;
}

#currentLocation {
  text-align: center;
  margin: 2rem auto;
  display: block;
  font-size: xx-large;
}

/* aside */

.rating {
  margin-left: 2rem;
  margin: 0rem 30%;
  width: 20%;
}

.rating label {
  font-size: x-large;
  font-weight: 500;
  display: block;
  margin: 0.1rem auto 1rem 1rem;
}

#ratingList {
  margin-top: 4rem;
  width: 50%;
  height: 40%;
  margin: 0 auto;
}

.svgClass {
  width: 20px;
  height: 20px;
  padding: 0%;
  margin: 0%;
  margin-right: 10px;
}

.hidden {
  display: none;
  visibility: hidden;
}

span.MuiStepLabel-alternativeLabel {
  font-size: x-large !important;
}
span.Mui-active {
  font-size: xx-large !important;
  font-weight: 700 !important;
}

.buttonGroup {
  display: flex;
  justify-content: space-between;
}
